:root {
  color-scheme: dark;
  font-family: "Inter", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  background: #140903;
  color: #ffe8c3;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  background: radial-gradient(circle at top, #3f1e0c 0%, #120703 70%);
  color: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

.content {
  width: 100%;
  margin: 0 auto;
  padding: clamp(24px, 5vw, 72px);
}

.card {
  position: relative;
  background: linear-gradient(145deg, #4b2b17, #2c150a);
  border: 1px solid #8b4f1f;
  border-radius: 28px;
  padding: 24px;
  margin-bottom: 24px;
  color: #ffe8c3;
  box-shadow: 0 25px 70px rgba(0, 0, 0, 0.45), inset 0 0 0 1px rgba(255, 255, 255, 0.05);
}


.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 16px;
}

.button {
  background: linear-gradient(180deg, #f6bd68, #c77431);
  color: #2d1408;
  border: 1px solid #703c14;
  border-radius: 999px;
  padding: 12px 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  transition: transform 0.15s, filter 0.15s;
}

.button:hover:not(:disabled) {
  transform: translateY(-2px);
  filter: brightness(1.05);
}

.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

input,
select,
textarea {
  border-radius: 12px;
  padding: 12px 14px;
  border: 1px solid rgba(255, 232, 195, 0.2);
  background: rgba(22, 11, 5, 0.9);
  color: inherit;
}

label {
  font-size: 14px;
  color: rgba(255, 232, 195, 0.8);
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.phaser-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.phaser-wrapper canvas {
  max-width: 100%;
  height: auto !important;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.5);
}

.virtual-pad {
  position: absolute;
  bottom: 24px;
  left: 24px;
  display: grid;
  grid-template-columns: repeat(3, 50px);
  grid-template-rows: repeat(3, 50px);
  gap: 8px;
  pointer-events: none;
}

.virtual-pad-button {
  pointer-events: auto;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  background: linear-gradient(180deg, #fbd085, #b25b1c);
  color: #2b1408;
  font-weight: 900;
  font-size: 18px;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.4);
}

.virtual-pad-button.pad-up {
  grid-column: 2;
  grid-row: 1;
}

.virtual-pad-button.pad-down {
  grid-column: 2;
  grid-row: 3;
}

